@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Lato", sans-serif;
}

.App {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
}

.invoice-container {
  margin-top: 2rem;
  padding: 2rem;
  box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.1);
  width: 800px;
}

.back-container {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
  cursor: pointer;
}

.back-btn {
  font-size: 28px;
  color: black;
  cursor: pointer;
}

.review-btn {
  padding: 0.5rem;
  background-color: rgb(32, 178, 204);
  border: none;
  color: white;
  border-radius: 3px;
  cursor: pointer;
}

.review-btn:hover {
  background-color: rgb(15, 162, 182);
}

.reset-btn {
  padding: 0.5rem;
  background-color: rgb(247, 30, 30);
  border: none;
  color: white;
  border-radius: 3px;
  cursor: pointer;
  margin-left: 0.5rem;
}

.reset-btn:hover {
  background-color: rgb(221, 23, 23);
}

@media (max-width: 560px) {
  .invoice-container {
    padding: 0.5rem;
  }
  * {
    font-size: 14px;
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --btn-border-color: rgb(204, 204, 204);
  --text-color: rgb(116, 116, 116);

  --bg-primary-color: #ebebeb;
}

.header {
  display: flex;
  justify-content: space-between;
}

.add-label {
  cursor: pointer;
  opacity: 0.8;
  background-color: #ebebeb;
  background-color: var(--bg-primary-color);
  border-radius: 5px;
  width: 150px;
  height: 120px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  border: 1.5px solid rgb(204, 204, 204);
  border: 1.5px solid var(--btn-border-color);
}
.logo {
  width: 100%;
  height: 100%;
  object-fit: cover;
  line-height: 120px;
  text-align: center;
  border-radius: 5px;
}
#add-photo {
  opacity: 0;
  position: absolute;
  z-index: -1;
  width: 150px;
  height: 120px;
}

.header > .right > h1 {
  font-size: 3rem;
  margin-bottom: 1rem;
  text-align: right;
}

.header > .right > button {
  padding: 0.5rem 0.7rem;
  font-size: 16px;
  border: 1px solid rgb(204, 204, 204);
  border: 1px solid var(--btn-border-color);
  border-radius: 3px;
}

.header > .right > input[type="text"] {
  padding: 0.5rem;
  text-align: right;
  margin-left: -5px;
  font-size: 16px;
  border: 1px solid rgb(204, 204, 204);
  border: 1px solid var(--btn-border-color);
  border-radius: 3px;
  width: 150px;
}

/* Styling Main section  */

.main {
  display: flex;
  justify-content: space-between;
  margin: 2rem 0 1rem 0;
  position: relative;
}

.main > .left > h4 {
  color: rgb(116, 116, 116);
  color: var(--text-color);
}

.main > .left > input[type="text"] {
  padding: 1.5rem;
  margin: 0.5rem 0 1.5rem 0;
  border: 1.5px solid rgb(204, 204, 204);
  border: 1.5px solid var(--btn-border-color);
  border-radius: 5px;
  font-size: 16px;
}

.warn-text {
  position: absolute;
  top: 50px;
  left: 32%;
  color: red;
  text-transform: capitalize;
}

.warn-text.t2 {
  top: 175px;
}
/* .main > .right {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
} */
.right.date-section {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.right input[type="date"] {
  padding: 0.5rem;
  margin: 0.5rem 0 0.5rem 1rem;
  border: 1.5px solid rgb(204, 204, 204);
  border: 1.5px solid var(--btn-border-color);
  border-radius: 5px;
}

.right label {
  color: rgb(116, 116, 116);
  color: var(--text-color);
}

@media (max-width: 560px) {
  .right.date-section div {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  .header > .right > input[type="text"] {
    width: 120px;
  }
  .main > .left > input[type="text"] {
    padding: 1rem;
  }
  .warn-text {
    top: 80px;
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --btn-border-color: rgb(204, 204, 204);
  --text-color: rgb(116, 116, 116);

  --btn-primary-color: rgb(84, 197, 84);
  --btn-hover-color: rgb(75, 163, 75);

  --delete-btn-color: #ec330e;
  --edit-btn-color: rgb(84, 197, 84);
}

table {
  width: 100%;
  border-spacing: 4px 4px;
}
th {
  text-align: left;
  padding: 0 10px;
}

tr p {
  font-weight: bold;
}

.item {
  transition: all 200ms ease-in;
}

.title {
  width: 400px;
  border: 1.5px solid rgb(204, 204, 204);
  border: 1.5px solid var(--btn-border-color);
  padding: 0.5rem 1rem;
  border-radius: 3px;
  color: black;
}

.qty {
  border: 1.5px solid rgb(204, 204, 204);
  border: 1.5px solid var(--btn-border-color);
  padding: 0.5rem 1rem;
  border-radius: 3px;
}

.rt {
  border: 1.5px solid rgb(204, 204, 204);
  border: 1.5px solid var(--btn-border-color);
  padding: 0.5rem 0.6rem;
  border-radius: 3px;
}

.rt > span {
  padding-right: 0.3rem;
}

input[type="text"] {
  width: 100%;
}

input[type="number"] {
  width: 100%;
}

input[type="text"],
input[type="number"] {
  padding: 0.7rem;
  border: 1.5px solid rgb(204, 204, 204);
  border: 1.5px solid var(--btn-border-color);
  border-radius: 3px;
  font-size: 14px;
}

tr:nth-child(1) > th:nth-child(2) {
  width: 100px;
}
tr:nth-child(1) > th:nth-child(3) {
  width: 100px;
}

tr:nth-child(2) > td:nth-child(2) {
  width: 80px;
}

tr:nth-child(1) {
  background-color: #ebebeb;
  height: 35px;
  border-radius: 5px;
}

.rate {
  display: flex;
  border: 1.5px solid rgb(204, 204, 204);
  border: 1.5px solid var(--btn-border-color);
  border-radius: 3px;
  width: 100px;
}
.rate > span {
  padding: 0.5rem 0 0.5rem 0.6rem;
}
.rate > .rate-input {
  border: none;
  width: 100%;
}

.rate > .rate-input:focus {
  outline: none;
}

.amount {
  padding: 0.5rem;
  color: rgb(116, 116, 116);
  color: var(--text-color);
  width: 100px;
}

.amt-title {
  width: 100px;
}

.edit {
  display: flex;
  padding: 0.5rem 0;
}

.edit-btn {
  width: 22px;
  cursor: pointer;
  color: rgb(84, 197, 84);
  color: var(--edit-btn-color);
  font-size: 22px;
  margin: 1px 5px;
}

.delete-btn {
  width: 24px;
  cursor: pointer;
  color: #ec330e;
  color: var(--delete-btn-color);
  font-size: 24px;
}

.add-item {
  padding: 0.45rem;
  border-radius: 3px;
  border: none;
  margin: 0.5rem 0 0.5rem 0.3rem;
  background-color: rgb(84, 197, 84);
  background-color: var(--btn-primary-color);
  color: white;
  font-size: 15px;
  cursor: pointer;
}
.add-item:hover {
  background-color: rgb(75, 163, 75);
  background-color: var(--btn-hover-color);
}

small {
  display: none;
}

@media (max-width: 560px) {
  .rate > span {
    padding: 0.6rem 0 0.5rem 0.6rem;
  }
  tr:nth-child(1) > th:nth-child(2) {
    width: 80px;
  }
  tr:nth-child(1) > th:nth-child(3),
  .rate {
    width: 80px;
  }

  tr:nth-child(2) > td:nth-child(2) {
    width: 80px;
  }
  .edit-btn,
  .delete-btn {
    width: 20px;
  }
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --btn-border-color: rgb(204, 204, 204);
  --text-color: rgb(116, 116, 116);

  --btn-primary-color: rgb(84, 197, 84);
  --btn-hover-color: rgb(75, 163, 75);
}

.invoice-result {
  display: flex;
  justify-content: space-between;
  position: relative;
}

.invoice-result > .left {
  margin-top: 2rem;
  width: 50%;
}
.invoice-result > .left > #description-box {
  width: 100%;
  border: 1.5px solid rgb(204, 204, 204);
  border: 1.5px solid var(--btn-border-color);
  border-radius: 5px;
  padding: 0.5rem;
  font-size: 16px;
  margin-top: 2rem;
}

.invoice-result > .right {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin-top: 2rem;
}

.invoice-result > .right > div {
  display: flex;
  margin-bottom: 0.5rem;
  align-items: center;
}

.invoice-result > .right div input[type="text"] {
  width: 100px;
  text-align: right;
}

input[type="text"]:focus {
  outline: none;
}

.invoice-result > .right p {
  position: absolute;
  right: 150px;
}

.tag {
  opacity: 0.7;
}

.discount > div > input[type="text"],
.tax > div > input[type="text"] {
  padding-right: 0;
  padding-top: 0.6rem;
}

.discount > p,
.tax > p {
  opacity: 0.7;
}

.amtpaid > p {
  opacity: 0.7;
}

.amtpaid > div,
.discount > div,
.tax > div {
  display: flex;
  border: 1.5px solid rgb(204, 204, 204);
  border: 1.5px solid var(--btn-border-color);
  border-radius: 5px;
  width: 140px;
  justify-content: flex-end;
}

.amtpaid > div > span,
.discount > div > span,
.tax > div > span {
  padding: 0.5rem 0.4rem;
  opacity: 0.7;
}

.amtpaid > div > input[type="text"],
.discount > div > input[type="text"],
.tax > div > input[type="text"] {
  text-align: right;
  border: none;
  border-radius: 5px;
}

@media (max-width: 560px) {
  .invoice-result > .right > div {
    width: 100px;
  }
  .invoice-result > .right p {
    position: absolute;
    right: 110px;
  }

  .invoice-result > .right div input[type="text"] {
    width: 100%;
  }

  .invoice-result > .right > div > span {
    margin-left: auto;
  }
}

